"use client";

import { useEffect, useState } from "react";

export default function CompletedOrdersPage() {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const getCompletedOrders = async () => {
      try {
        const response = await fetch("/api/order/completed");
        if (!response.ok) {
          alert("Áç≤ÂèñÂ∑≤ÂÆåÊàêË®ÇÂñÆÂ§±Êïó");
          return;
        }

        const data = await response.json();
        setOrders(data);
        setLoading(false);
      } catch (err) {
        alert("ËÆÄÂèñÈåØË™§");
      }
    };

    getCompletedOrders();
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-100 to-gray-300 py-10 px-6">
      <div className="max-w-5xl mx-auto">
        <h1 className="text-3xl font-bold text-gray-800 mb-8">üì¶ Â∑≤ÂÆåÊàêÁöÑË®ÇÂñÆ</h1>

        {loading ? (
          <div className="space-y-4">
            {[...Array(3)].map((_, i) => (
              <div
                key={i}
                className="animate-pulse h-24 bg-white rounded-lg shadow"
              />
            ))}
          </div>
        ) : orders.length === 0 ? (
          <div className="text-center text-gray-500 mt-20 text-lg">
            Êö´ÊôÇÊ≤íÊúâÂ∑≤ÂÆåÊàêÁöÑË®ÇÂñÆ
          </div>
        ) : (
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
            {orders.map((order) => (
              <div
                key={order.id}
                className="bg-white rounded-xl shadow-md p-5"
              >
                <h2 className="text-xl font-bold text-green-700 mb-2">
                  Ë®ÇÂñÆ #{order.id.slice(0, 8)}
                </h2>
                <p className="text-gray-800 font-medium mb-1">
                  È°ßÂÆ¢Ôºö{order.customer?.name}
                </p>
                <ul className="text-sm list-disc pl-5 mb-2 space-y-1">
                  {order.items.map((item, idx) => (
                    <li key={idx}>
                      {item.menuItem.name}
                      {item.specialRequest && (
                        <p className="text-xs text-gray-500 ml-4">
                          ÂÇôË®ªÔºö{item.specialRequest}
                        </p>
                      )}
                    </li>
                  ))}
                </ul>
                <p className="text-xs text-gray-500">
                  ÂÆåÊàêÊôÇÈñìÔºö{new Date(order.completedAt).toLocaleString()}
                </p>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
